.profile {
    display: flex;
    width: var(--container-width-lg);
    justify-content: space-around;
    margin: auto;
    color: var(--color-tertiary);
    border: 10px solid var(--color-white);
    border-radius: 10px;
}
.profilemenu {
    width: 30%;
    background-color: var(--color-white);
    /* border: 10px solid var(--color-white); */
    border-radius: 10px;
    margin-right: 10px;
}

.profback {
    width: 100%;
    border-radius: 10px;
}

.profimg {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 50px;
    margin-top: -60px;
    border: 5px solid var(--color-secondary-shade1)
}

.profName {
    color: var(--color-tertiary);
}

.manuopt {
    font-weight: bolder;
    padding: 10px;
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: left;
    color: var(--color-tertiary);
    border-bottom: 1px solid var(--color-tertiary-tint1);
}

.subopt {
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.manuopt1 {
    padding: 12px;
    width: 100%;
    /* flex: 1 0 auto; */
    color: var(--color-tertiary);
    font-weight: bolder;
}

.menuitemhere {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-content: bottom;
    margin-top: 90px;
}

.profileproduct {
    width: 70%;
    background-color: var(--color-white);
    display: flex;
    justify-content: space-between;
    /* padding: 20px; */
    align-content: center;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    list-style-type: none;
}

.eachproduct {
    width: 290;
    height: 370;
    border-radius: 10px;
    margin: 10px;
    display: flex;
    flex-direction: column;
    padding: 5px;
    align-content: center;
    box-shadow: 1px 2px var(--color-tertiary-tint2);
}

.scale {
    font-size: smaller;
}

/* .eachproduct:hover {
    border: 2px solid var(--color-secondary-shade1);
} */

.productImage {
    width: 150px;
    height: 120px;
    object-fit: cover;
    align-self: center;
    margin: auto;
}

.farmname {
    font-size: 10px;
    align-self: flex-start;
}

.productname {
    font-size: medium;
    font-weight: bold;
    align-self: flex-start;
}

.ratingc {
    display: flex;
    color: var(--color-secondary-shade1);
    justify-content: flex-start;
    align-content: center;
}

.rating {
    font-size: 8px;
    color: var(--color-tertiary);
    align-self: center;
}

.editoptions {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    margin: 10px 10px;
    color: var(--color-white);
}

.editproduct {
    background-color: var(--color-primary);
    padding: 5px;
    border-radius: 5px;
    width: 58px;
    color: var(--color-white);
}

.deletproduct {
    background-color: var(--color-danger-variant);
    padding: 5px;
    border-radius: 5px;
    color: var(--color-white);
    width: 58px;
}

@media (max-width:1100px) {
    .profilemenu {
        display: none;
        visibility: hidden;
    }

    .profileproduct {
        width: 100%
    }
}