.signContainer {
    max-width: var(--container-width-lg);
    min-width: var(--container-width-sm);
    min-height: 100vh;
    display: flex;
    margin: auto auto;
    align-items: center;
    justify-content: center;
}

.signutarget {
    flex-basis: 50%;
    align-items: center;
    background-color: var(--color-primary);
    padding: 120px 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.signufform {
    flex-basis: 50%;
    background-color: var(--color-white);
}

.signueach {
    justify-content: space-between;
    color: var(--color-white);
    margin-top: 50px;
    border: 2px solid var(--color-white);
    min-width: 80%;
    align-content: center;
    padding: 10px;
}


.signueach1 {
    color: var(--color-white);
    display: flex;
    justify-content: space-between;
}

.already {
    color: var(--color-white);
    margin: 30px;
}

.signlink {
    color: var(--color-secondary);
}

.lohosm {
    width: 20%;
    justify-content: start;
}

.signutargetm{
    display: none;
    visibility: hidden;
}


.signtext {
    display: none;
    visibility: hidden;
}

.form_inp {
    border-bottom: 1px solid var(--color-tertiary);
    border-radius: 0;
}

.sign_form {
    width: 70%;
    margin: auto;
}

.sign_bt {
    width: 90%;
    padding: 10px;
    background-color: var(--color-secondary);
    border-radius: 1px;
    font-weight: bolder;
    color: var(--color-white);
}

.post_sign {
    align-self: center;
}

.already1 {
    font-size: small;
    padding: 10px;
    margin-bottom: 40px;
}

.signalt {
    color: var(--color-secondary);
    font-weight: bold;
}

@media(max-width:870px) {
    .signutarget {
        display: none;
        visibility: hidden;
    }

    .signtext {
        display: block;
        visibility: visible;
        color: var(--color-primary);
    }

    .signutargetm {
        display: flex;
        visibility: visible;
        align-items:center;
        justify-content: space-between;
        width: 90%;
        padding: 10px;
        /* margin: 10px 40px; */
    }
    
    .signueachm {
        width: 100px;
        color: var(--color-primary);
        border: 1px solid var(--color-primary);
        border-radius: 5px;
        height: 70px;
        margin: 10px 10px;
    }
    
    .signueach1ms {
        width: 90px;
        color: var(--color-white);
        border: 1px solid var(--color-primary);
        border-radius: 5px;
        height: 70px;
        background-color: var(--color-primary);
    }
    
    .signutargetm {
        font-size:large;
        font-weight: medium;
        text-align: center;
        padding: 10px;
        color: var(--color-primary);
    }

    .sign_form {
        width: 100%;
        margin: auto;
    }
}